      <span class="hljs-tag">&lt;<span class="hljs-name">vc-datasource-kml</span> <span class="hljs-attr">data</span>=<span class="hljs-string">"./statics/SampleData/kml/gdpPerCapita2008.kmz"</span> <span class="hljs-attr">:show</span>=<span class="hljs-string">"show"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"clicked"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-datasource-kml</span>&gt;</span>
