<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>changedEvent</td>
<td></td>
<td>数据源改变时触发。</td>
</tr>
<tr>
<td>errorEvent</td>
<td></td>
<td>数据源发生错误时触发。</td>
</tr>
<tr>
<td>loadingEvent</td>
<td></td>
<td>数据源开始或结束加载时触发。</td>
</tr>
<tr>
<td>clusterEvent</td>
<td>(clusteredEntities, cluster)</td>
<td>数据源聚合事件。</td>
</tr>
<tr>
<td>collectionChanged</td>
<td>(collection, added, removed, changed)</td>
<td>数据源实体集合改变时触发。</td>
</tr>
<tr>
<td>mousedown</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标在该数据源上按下时触发。</td>
</tr>
<tr>
<td>mouseup</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标在该数据源上弹起时触发。</td>
</tr>
<tr>
<td>click</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标单击该数据源时触发。</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标左键双击该数据源时触发。</td>
</tr>
<tr>
<td>mousemove</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标移动到该数据源时触发。</td>
</tr>
</tbody>
</table>
